@import '../../../base';

.piggy-container {
  border: solid 1px $color-line;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 26px 20px !important;

  @media screen and (max-width: 568px) {
    flex-wrap: wrap;
    padding-top: 12px !important;
    padding-bottom: 20px !important;

    .piggy-img-container {
      flex: 0 0 50%;
      justify-content: center;
      align-items: center;
      display: flex;
    }
  }

  .wrap-container {
    flex-wrap: wrap;
    padding: 0;
    align-items: center;
  }

  .piggy-right-container {
    padding-left: 22px;
    flex: 1;
    @media screen and (max-width: 568px) {
      padding-left: 0;
      flex: 0 0 50%;
    }

    .piggy-desc-container {
      .main-text {
        margin-top: 10px;
        font-size: 20px;
        font-weight: $font-weight-semibold;
        color: $color-hover;
        line-height: 15px;

        @media screen and (max-width: 568px) {
          font-size: $font-size-regular;
        }

        img {
          margin: 0 5px;
          max-width: 26px;
          max-height: 26px;
        }
      }
    
      .desc-text {
        font-size: 13px;
        font-weight: $font-weight-normal;
        color: $color-body;
        line-height: normal;

        @media screen and (max-width: 468px) {
          font-size: 10px;
        }
      }

      .desc-padding {
        margin-top: -5px;
        margin-bottom: 0;
      }

      @media screen and (max-width: 568px) {
        .main-text {
          line-height: 18px;
        }
      }
    }
  }

  .button-container {
    margin-top: 16px;
    flex: 100%;
    @media screen and (max-width: 568px) {
      flex: 0 0 100%;
      .button-medium-color {
        min-height: 36px;
        padding: 10px 15px !important;
      }
    }

    .button-medium-color {
      width: 100%;
      min-width: 0 !important;;

      span {
        margin-right: 10px;
      }
      img {
        margin: 0 3px;
      }

      @media screen and (max-width: 460px) {
        padding: 20px;
      }
    }
  }
}