 @import '../../../base';
.datepicker {
  .date-img {
    padding-right: 10px;
    padding-bottom: 8px;
  }

  .date-img-in {
    width: 32px;
    height: 32px;
  }

  .date-img-out {
    width: 38px;
    height: 38px;
  }
}

.date-range-picker {
  .input-wrapper-main {
    margin: 0;
  }
}