@import '../../../base';

.detail-main-container {
  max-width: 1036px;
  margin: auto;
  margin-top: 20px;
  padding: 0 15px;

  .detail-left-container {
    max-width: 780px;
    min-height: 500px;
    background-color: white;

    .main-form {
      .text-input-main {
        margin-top: 50px;

        .text-sel-col:nth-child(2) {
          padding-right: 10px !important;
        }
  
        .text-sel-col:nth-child(3) {
          padding-left: 10px !important;
        }
        
        
        @media screen and (max-width: 576px) {
          .text-sel-col:nth-child(2) {
            padding-right: 0 !important;
          }
    
          .text-sel-col:nth-child(3) {
            margin-top: 5px;
            padding-left: 0 !important;
          }
        }
      }
    }

    .main-form-top-margin {
      margin-top: 20px;
    }
  }

  .right-scroll {
    @media screen and (min-width: 1040px) {
      position: absolute;
      margin-left: 810px !important;
    }
  }

  .right-sticky {
    @media screen and (min-width: 1040px) {
      position: fixed;
      top: 84px;
      margin-left: 810px !important;
      z-index: 1;
    }
  }

  .upper-right-container {
    margin-left: 30px;
    max-width: 196px;
    // max-height: 450px;
    box-shadow: 0 30px 40px 0 rgba(133, 195, 255, 0.15);
    background-color: white;
    border: solid 1px $color-line;

    .logo-container {
      min-height: 191px;
      border-bottom: solid 1px $color-line;
      display: flex;
      flex-direction: column;
      padding: 20px;

      .logo-inner-container {
        min-height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        .company-logo {
          max-height: 80px;
          max-width: 80px;
        }
      }
      
      p {
        text-align: center;
        font-size: $font-size-regular;
        font-weight: $font-weight-semibold;
        color: $color-body;
        margin: auto;
        margin-top: 0;
        word-break: break-all;
      }
    }

    .submit-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 33px;

      .sign-up-container {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .total-text {
          font-size: $font-size-regular;
          font-weight: $font-weight-normal;
          color: $color-caption;
          align-self: flex-start;
          margin: 0;
          line-height: normal;
        }
  
        .cost-text {
          text-align: center;
          font-size: $font-size-extra-big;
          font-weight: $font-weight-heavy;
          color: $color-title;
          line-height: normal;
        }
      }
      

      .sign-up {
        width: 100%;
        margin-bottom: 20px;
        align-self: flex-start;
      }

      

      button {
        margin-bottom: 10px;
        width: 130px;

        p {
          font-size: $font-size-small;
        }
      }
    }
  }

  .header {
    font-size: $font-size-big;
    font-weight: $font-weight-heavy;
    color: $color-title;
    margin: 15px 0 -10px 0;
  }

  .right-padding {
    padding-right: 20px;
  }
  .left-padding {
    padding-left: 20px;
  }

  @media screen and (max-width: 1040px) {
    .detail-left-container{
      max-width: none;
      width: 100%;
    }
    
    .upper-right-container {
      position: fixed;
      min-height: auto;
      bottom: 0;
      left: 0;
      width: 100%;
      max-width: none;
      margin-left: 0;
      background-color: white;
      z-index: 9999;
      box-shadow: 0 -5px 6px 0 rgba(70, 115, 122, 0.1);
      border: none;

      .logo-container {
        display: none;
      }

      .submit-container {
        display: flex;
        flex-direction: row;
        padding: 11px 15px;
        
        .sign-up-container {
          width: auto;
          flex-direction: row;
          max-width: 500px;
          display: flex;
          justify-content: center;
          align-items: center;

          .cost-container {
            .total-text {
              align-self: flex-start;
              font-size: 10px;
              line-height: 5px;
            }
      
            .cost-text {
              align-self: flex-start;
              font-size: $font-size-big;
              margin: 0;
            }
          }
    
          .sign-up {
            width: 33%;
            margin: 0;
            display: flex;
            height: 100%;
            justify-content: flex-end;
            align-items: center;
            padding-right: 15px;

            .selection {
              width: 125px;

              
              .single-selection {
                margin: 0 20px;
              }

              .checkbox-outer-default {
                margin-top: 2px;
              }

              .description {
                font-size: $font-size-big * 0.5;
                margin: auto;
              }
            }
          }
        }

        .no-total {
          width: 100% !important;
          .input-wrapper-main {
            margin-top: 5px;
          }
        }

        button {
          min-width: 80px !important;
          width: 80px;
          min-height: 36px !important;
          padding: 10px 0 !important;
          margin-bottom: 0;
          margin-left: 10px;
      
          p {
            font-size: 10px !important;
          }
        }

        .button-medium-grey:nth-of-type(2) {
          display: none;
        }
      }
    }
    .right-padding {
      padding-right: 10px;
    }
    .left-padding {
      padding-left: 10px;
    }
  }

  @media screen and (max-width: 576px) {
    .right-padding {
      padding-right: 0;
    }
    .left-padding {
      padding-left: 0;
    }
  }

  .input-desc {
    font-size: $font-size-regular;
    font-weight: $font-weight-normal;
    color: $color-body;
  }

  .hr-margin {
    margin-top: 30px;
  }

  .error-string {
    margin-top: 20px;
    font-size: $font-size-regular;
    font-weight: $font-weight-semibold;
    color: $color-error-alt;

    .error-sub {
      display: block;
    }
  }
}
