@import '../../../base';
$primary-color: #00dfa4;
$dark-primary-color: #46737a;
$secondary-color: #c8e0e4;

%radio-outer {
  position: relative;
  flex-shrink: 0;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: white;
  border: 1px solid $secondary-color;
  margin-right: 8px;
  &:focus {
    outline: none;
  }
}

%radio-inner {
  transition: all 0.3s ease;
  position: relative;
  top: 5px;
  left: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
}

%checkbox-outer {
  @extend %radio-outer;
  border-radius: 25%;
  margin-top: 6px;
  &:focus {
    outline: none;
  }
}

%checkbox-inner {
  transition: all 0.3s ease;
  position: absolute;
  top: 3px;
  left: 3px;
  width: 16px;
  height: 16px;
}

%text-selection {
  width: 100%;
  min-height: 60px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f2faf9;
  border: 1px solid $secondary-color;
  &:focus {
    outline: none;
  }
}

.selection {
  .text-sel-container {
    padding: 0px 15px;
  }

  .text-sel-col {
    @media screen and (max-width: 576px) {
      padding: 0 !important;
      margin-bottom: 5px;
    }
  }
  
  .text-sel-col {
    margin-bottom: 10px;
    padding: 0 5px;
    text-align: center;
  }
  .single-selection {
    display: flex;
    height: 100%;
    // margin-top: 8px;

    .radio-outer-default {
      @extend %radio-outer;
      background: white;
    }

    .radio-outer-highlighted {
      @extend %radio-outer;
      background: linear-gradient(132deg, #00d7af, #46737a);
    }

    .radio-inner-default {
      transition: all 0.3s ease;
      @extend %radio-inner;
      transform: scale(0);
      opacity: 0,
    }

    .radio-inner-highlighted {
      transition: all 0.3s ease;
      @extend %radio-inner;
      transform: scale(1);
      opacity: 1;
    }

    .checkbox-outer-default {
      @extend %checkbox-outer;
    }

    .checkbox-outer-highlighted {
      @extend %checkbox-outer;
      background: linear-gradient(132deg, #00d7af, #46737a);
      border: 0px;
    }

    .checkbox-inner-default {
      transition: all 0.3s ease;
      @extend %checkbox-inner;
      transform: scale(0);
      opacity: 0,
    }

    .checkbox-inner-highlighted {
      transition: all 0.3s ease;
      @extend %checkbox-inner;
      transform: scale(1);
      opacity: 1;
    }

    .text-selection-outer-default {
      @extend %text-selection;
      .description {
        color: $color-caption;
      }
      &:hover {
        cursor: pointer;
      }
    }

    .text-selection-outer-highlighted {
      @extend %text-selection;
      .description {
        color: $color-title;
      }
      box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
      border: solid 2px transparent;
      background-image: linear-gradient(#f2faf9, #f2faf9), linear-gradient(110deg, #00d7af, #46737a);
      background-origin: border-box;
      background-clip: content-box, border-box;
      &:hover {
        cursor: pointer;
      }
    }

    .description {
      padding: 5px;
      color: $dark-primary-color;
      font-weight: $font-weight-semibold;
    }
  }
}